<template>
  <div class="product-list" >
    <!-- 搜索 -->
    <Search @submit="searchSubmit"
    :categoryList="categoryList"/>
    <a-button class="product-app-btn">
      <router-link :to="{name: 'ProductAdd'}">添加商品</router-link>
    </a-button>
    <!-- 表格 -->
    <products-table
    :tableData="productTableData"
    :page="page"
    @change="changePage"
    @edit="editProduct"
    @remove="removeProduct"
    :categoryList="categoryList"
    />
  </div>
</template>

<script>
import Search from '@/components/Search.vue';
import productsTable from '@/components/productsTable.vue';
import api from '@/api/product';
import categoryApi from '@/api/category';

export default {
  data() {
    return {
      tableData: [],
      searchForm: {},
      categoryList: [],
      page: {
        current: 1,
        pageSize: 10,
        showSizeChanger: true,
        total: 1,
      },
      categoryObj: {},
    };
  },
  components: {
    Search,
    productsTable,
  },
  computed: {
    productTableData() {
      return this.tableData.map((item) => ({
        ...item,
        key: item.id,
      }));
    },
  },
  async created() {
    await categoryApi().then((res) => {
      this.categoryList = res.data;
      res.data.forEach((item) => {
        this.categoryObj[item.id] = item;
      });
    });
    this.getTableData();
  },
  methods: {
    searchSubmit(form) {
      this.searchForm = form;
      this.getTableData();
    },
    getTableData() {
      api.list({
        page: this.page.current,
        size: this.page.pageSize,
        ...this.searchForm,
      }).then((res) => {
        this.page.total = res.total;
        this.tableData = res.data.map((item) => ({
          ...item,
          categoryName: this.categoryObj[item.category].name,
        }));
      });
    },
    changePage(page) {
      this.page = page;
      this.getTableData();
    },
    editProduct(record) {
      this.$router.push({
        name: 'ProductEdit',
        params: {
          id: record.id,
        },
      });
    },
    removeProduct(record) {
      const self = this;
      this.$confirm({
        title: '确认删除',
        content: () => <div style="color:red;">{`确认删除:${record.title}改商品吗?`}</div>,
        onOk() {
          api.remove({
            id: record.id,
          }).then((res) => {
            window.console.log(res);
            self.getTableData();
          });
        },
        onCancel() {
          window.console.log('Cancel');
        },
        class: 'config-box',
      });
    },
  },
};

</script>

<style lang='less'>
.product-list {
  position: relative;
  .product-app-btn {
    position: absolute;
    right: 0px;
    top: 14px;
 }
}

</style>
